.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 40px;

  @include vp-1920 {
    width: 100%;
    max-width: size(1440px);
    margin: 0 auto;
    padding: 0 size(40px);
  }

  @include vp-1023 {
    padding: 0 size(24px);
  }

  @include vp-767 {
    padding: 0 size(20px);
  }
}